@mixin titleMargin {
  margin: 0;
}

@mixin flexCenter {
  display: flex;
  justify-content: center;
  align-items: center;
}

@mixin listMargin {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

@mixin linkStyle {
  text-decoration: none;
}

@mixin paragraphMargin {
  margin: 0;
}
